.letterfx {
	display: inline-block;
	margin: 0;
	padding: 0;
	transition: all 1s;
	-ms-transition: all 1s;
	-webkit-transition: all 1s;
}
.letterfx-spin-before {
	transform: rotate(3600deg);
	-ms-transform: rotate(3600deg);
	-webkit-transform: rotate(3600deg);
}
.letterfx-spin-after {
	transform: none;
}
.letterfx-fade-before {
	opacity: 0;
}
.letterfx-grow-before {
	transform: scale(0, 0);
	-ms-transform: scale(0, 0);
	-webkit-transform: scale(0, 0);
}
.letterfx-grow-after {
	transform: none;
}
.letterfx-smear-before {
	color: transparent;
	text-shadow: -1px -1px 25px transparent;
}
.letterfx-smear-after {
	color: inherit;
	text-shadow: 0 0 #333;
}
.letterfx-fall-before {
	visibility: hidden;
	transform: scale(3, 3);
	-ms-transform: scale(3, 3);
	-webkit-transform: scale(3, 3);
}
.letterfx-fall-after {
	transform: scale(1, 1);
	-ms-transform: scale(1, 1);
	-webkit-transform: scale(1, 1);
	text-shadow: 0;
}
.letterfx-swirl-before {
	visibility: hidden;
	transform: scale(3, 3) rotate(3600deg);
	-ms-transform: scale(3, 3) rotate(3600deg);
	-webkit-transform: scale(3, 3) rotate(3600deg);
}
.letterfx-swirl-after {
	transform: none;
	text-shadow: 0;
}
.letterfx-wave-container .letterfx {
	position: relative;
}
.letterfx-wave-before {
	bottom: 0;
}
.letterfx-wave-after {
	bottom: 15px;
}
